.haitem_navbar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 6rem;
}
.haitem_navbar-links{
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.haitem_navbar-links_logo{
    margin-right: 2rem;
    margin-top: 0.6rem;
}

.haitem_navbar-links_logo img{
   width: 80px;
   height: 40px;
}

.haitem_navbar-links_container {
    display: flex;
    flex-direction: row;
}

.haitem_navbar-links_container p,
.haitem_navbar-menu_container p {
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    text-transform: capitalize;
    margin:  0 3rem;
    padding: 0.5rem 1rem;
    cursor: pointer;

    transition: all 0.2s;
    transition-delay: 0.1s;
}
.haitem_navbar-links_container p::before,
.haitem_navbar-menu_container p::before {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 2px; 
    transition: all  0.2s;
  }
  .haitem_navbar-links_container p:hover,
  .haitem_navbar-menu_container p:hover {
    background-color: var(--color-subtext);
    transform: scale(1.1);
  }
  .haitem_navbar-links_container p:hover::before
   {
    bottom: 56px;
    left: 0;
    background-color: var(--color-subtext);
}
.haitem_navbar-menu {
    margin-left: 1rem;
    display: none;
    position: relative;
}

.haitem_navbar-menu svg {
    cursor: pointer;
}

.haitem_navbar-menu_container {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    text-align: end;
    background: var(--color-footer);
    padding: 1rem;
    position: absolute;
    top: 0px;
    right: 0;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow:  0 0 5 rgba(0,0,0.2);
}

.haitem_navbar-menu_container p {
    margin: 1rem 0;
}

@media screen and ( max-width: 1050px) {
    .haitem_navbar-links_container {
        display: none;
    }
    .haitem_navbar-menu {
        display: flex;
    }
    .haitem_navbar-menu_container {
        top: 20px;
    }
}
@media screen and ( max-width: 700px) {
    .haitem_navbar {
        padding: 2rem 4rem;
    }
    .haitem_navbar-menu_container {
        top: 20px;
    }
}
@media screen and ( max-width: 550px) {
    .haitem_navbar {
        padding: 2rem;
    }
    .haitem_navbar-menu_container {
        top: 20px;
    }
}
